import React from 'react';
function Todo({text, todo, todos , setTodos}) {
    const deleteHandler = () =>
    {
        setTodos(todos.filter((el) => el.id !== todo.id));
    };
    const changeText = () =>
    {
        setTodos(todos.map((item) => {
            if(item.id === todo.id)
            {
                const newText = prompt('Wpisz nowy tekst');
                return{
                    ...item, text: newText
                }
            }
            return item;
        }));
    };
    const setCompleteHandler = () =>
    {
        setTodos(todos.map((item) => {
            if(item.id === todo.id)
            {
                return{
                    ...item, completed: !item.completed
                }
            }
            return item;
        }));
    }
    const setpriorityHandler = () =>
    {
        setTodos(todos.map((item) => {
            if(item.id === todo.id)
            {
                return{
                    ...item, priority: !item.priority
                }
            }
            return item;
        }));
    }

    return (
        <div className={`todo ${todo.completed ? "completed" : ""} ${todo.priority ? "priority" : ""}`}>
            <li className="todo-item"><p>{text}</p></li>
            <div className="btn-place">
                <button onClick={setCompleteHandler} className="btn completed-btn" title="Zakończ">
                    <i className="fas fa-check"></i>
                </button>
                <button onClick={changeText} className="btn edit-btn" title="Edytuj">
                <i className="fas fa-edit"></i>
                </button>
                <button onClick={setpriorityHandler} className="btn priority-btn" title="Piorytet">
                    <i className="fas fa-exclamation"></i>
                </button>
                <button onClick={deleteHandler} className="btn trash-btn"  title="Zakończ">
                    <i className="fas fa-trash"></i>
                </button>
            </div>
        </div>
    );
}

export default Todo;
