import React from 'react'

function Form({inputText, setStatus, setInputText, todos, setTodos}) {
    const inputTextHandler = (e) => {
        setInputText(e.target.value);
    };
    const submitTodoHandler = (e) => {
        e.preventDefault();
        setTodos([
            ...todos,{text: inputText, priority: false, completed: false, id: Math.random()*1000}
        ]);
        setInputText("");
    };
    const statusHandler = (e) =>{
        setStatus(e.target.value)
    };
    return (
        <form>
            <input className="text" value={inputText} onChange={inputTextHandler} type="text"/>
            <button onClick={submitTodoHandler} className="todo-button" type="submit">
                <i className="fas fa-plus-square"></i>
            </button>
            <div className="select">
                <select onChange={statusHandler}>
                    <option value="all">Wszystko</option>
                    <option value="done">Dokończone</option>
                    <option value="undone">Niedokończone</option>
                    <option value="priority">Piorytetowe</option>
                </select>
            </div>
        </form>
    )
}

export default Form
